.logo:hover path {
  fill: white;
}
/*# sourceMappingURL=Logo.css.map */
a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: white;
}

nav {
  display: flex;
  background-color: lightgray;
  font-family: "Quicksand", sans-serif;
  font-weight: 600;
}

nav .logo-container {
  width: 3%;
}

nav .container {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
}

nav .container li {
  list-style: none;
  padding-right: 2%;
}
